import { Component, OnInit } from '@angular/core';
import { UserActivationService } from './user-activation.service';
import { Router } from '@angular/router';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { PluginService } from '../../../shared/plugin/plugin.service';

@Component({
    selector: 'user-activate',
    template: '<div></div>'
})
export class UserActivationComponent implements OnInit {
    key: any;

    constructor(
        private userService: UserActivationService,
        private router: Router,
        private pluginService: PluginService
    ) { }

    ngOnInit() {
        let routeUrl = this.router.url;
        let splitUrl = routeUrl.split('/');
        this.key = splitUrl[2];
        if (this.key) {
            this.userService.checkUserActivate(this.key).subscribe(
                (res: HttpResponse<any>) => this.onSucess(res.body),
                (res: HttpErrorResponse) => this.onError(res.message)
            )
        }
    }

    onSucess(response) {
        this.pluginService.alertMessage("User activated successfully", "success");
        this.router.navigateByUrl('/account/login');
    }

    onError(error) {
        this.pluginService.alertMessage(error, "error");
    }
}