import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

const headers = {
    'content-type': 'application/json',
    'app_id': 'rajendra_insightus_in_2bbc89',
    'app_key': '530248e6d18b74e17870'
}

@Injectable({ providedIn: 'root' })
export class OCRService {
    public ocrURL = "https://api.mathpix.com/v3/latex";

    constructor(private http: HttpClient) { }

    //MathPix Original request
    // {
    //     "src": "data:image/jpeg;base64,...",
    //     "ocr": ["math", "text"], 
    //     "skip_recrop": true,
    //     "formats": [
    //       "text",
    //       "latex_simplified",
    //       "latex_styled",
    //       "mathml",
    //       "asciimath",
    //       "latex_list"
    //      ]
    //   }

    processBase64IntoTex(base64: any): Observable<any> {
        let dataSource = {
            "src": base64,
            "ocr": ["math", "text"],
            "skip_recrop": true,
            "formats": [
                "latex_styled",
            ]
        }
        return this.http.post<any>(this.ocrURL, dataSource, { headers, observe: 'response' });
    }
}