import { Injectable, OnInit } from '@angular/core';
import { HttpClient, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { IUserLoginInfo, UserLoginInfo } from '../model/common/user.login.info.model';
import { PluginService } from '../plugin/plugin.service';
import { AccountService, Principal } from '../../../app/core';
import { CoreErrorHandler } from '../error-handler/error-handler.service';
import { CommonLocalStorageService } from '../local-storage/local-storage.service';

@Injectable({ providedIn: 'root' })
export class UserLoginInfoService implements OnInit {
    userId: number;
    schoolId: number;
    organisationId: number;
    login: string;
    roleAuthorities: any;

    constructor(
        protected http: HttpClient,
        public accountService: AccountService,
        public pluginService: PluginService,
        public principalService: Principal,
        public localStorageService: CommonLocalStorageService,
        public errorHandler: CoreErrorHandler
    ) {
    }

    ngOnInit() {
        if (this.principalService.authenticated === true) {
            this.getUserLoginInfo();
        }
    }

    getUserLoginInfo() {
        let userLoginInfo: IUserLoginInfo[] = [];
        userLoginInfo = this.localStorageService.retrieveData("userLoginInfo")
        this.userId = userLoginInfo[0].id;
        this.schoolId = userLoginInfo[0].schoolId;
        this.organisationId = userLoginInfo[0].organisationId;
        this.login = userLoginInfo[0].login;
        this.roleAuthorities = userLoginInfo[0].authorities;
    }
}