import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { IUsers } from '../../shared/model/common/users.model';
import { IUserLoginInfo } from '../../shared/model/common/user.login.info.model';
import { appUsers, account } from '../../shared/constants/service.constants';

type EntityResponseType = HttpResponse<IUserLoginInfo>;

@Injectable({ providedIn: 'root' })
export class AccountService {
    public appUserResourceUrl = environment.API_URL + appUsers;
    public accountResourceUrl = environment.API_URL + account;

    constructor(private http: HttpClient) { }

    get(): Observable<HttpResponse<IUsers>> {
        return this.http.get<IUsers>(this.accountResourceUrl, { observe: 'response' });
    }

    save(account: any): Observable<HttpResponse<any>> {
        return this.http.post(this.accountResourceUrl, account, { observe: 'response' });
    }

    getUserLoginInfo(login): Observable<EntityResponseType> {
        return this.http.get<IUserLoginInfo>(`${this.appUserResourceUrl}/${login}`, { observe: 'response' });
    }
}
