import { Component } from '@angular/core';
import { PluginService } from '../plugin/plugin.service';
import { OCRService } from './ocr.service';
import { Observable } from 'rxjs';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';

@Component({
    selector: "ocr",
    templateUrl: "./ocr.component.html"
})
export class OCRComponent {
    ckEditorMathPix: any;
    dataSource: any;
    generatedFormulaText: any;

    constructor(public pluginService: PluginService,
        public ocrService: OCRService) {
    }

    ngOnInit() {
        this.pluginService.sideMenuToggle();
        this.ckEditorMathPix = this.pluginService.getCKEditorMathPixConfig();
    }

    async getBase64ImageFromUrl(imageUrl) {
        var res = await fetch(imageUrl);
        var blob = await res.blob();
        return new Promise((resolve, reject) => {
            var reader = new FileReader();
            reader.addEventListener("load", function () {
                resolve(reader.result);
            }, false);

            reader.onerror = () => {
                return reject(this);
            };
            reader.readAsDataURL(blob);
        })
    }

    async onImageUpload() {
        let imageTags: any, htmlElement, imageUrl, base64;
        htmlElement = document.createElement("div");
        htmlElement.innerHTML = this.dataSource;
        imageTags = htmlElement.getElementsByTagName("img");
        if (imageTags.length > 0) {
            for (let i = 0; i < imageTags.length; i++) {
                imageUrl = imageTags[i].src; //Get image source 
                base64 = await this.getBase64ImageFromUrl(imageUrl);
                this.subscribeSaveResponse(this.ocrService.processBase64IntoTex(base64));
            }
        }
    }

    protected subscribeSaveResponse(result: Observable<HttpResponse<any>>) {
        result.subscribe((res: HttpResponse<any>) => this.onSaveResponse(res), (errRes: HttpErrorResponse) => this.onSaveError(errRes));
    }

    public onSaveResponse(res) {
        let error = res.body.error;
        this.generatedFormulaText = res.body.latex_styled;
        if (error) {
            this.pluginService.alertMessage(error, "error");
        }
    }

    public onSaveError(res) {
        this.pluginService.alertMessage(res, "error");
    }

    onCopyFormulaText() {
        let copyText: any = document.getElementById("generatedCode");
        copyText.select();
        copyText.setSelectionRange(0, 99999)
        document.execCommand("copy");
    }
}