import { NgModule, ErrorHandler } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { NgSelectModule } from '@ng-select/ng-select';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { AlifeFileToBase64Module } from 'alife-file-to-base64';
import { CKEditorModule } from 'ckeditor4-angular';
import { CoreErrorHandler } from './error-handler/error-handler.service';
import { TooltipModule } from 'ng2-tooltip-directive';

@NgModule({
    imports: [
        FormsModule,
        CommonModule,
        NgSelectModule,
        DragDropModule,
        AlifeFileToBase64Module,
        CKEditorModule,
        TooltipModule
    ],
    exports: [FormsModule, CommonModule, NgSelectModule, DragDropModule, AlifeFileToBase64Module, CKEditorModule, TooltipModule],
    providers: [{ provide: ErrorHandler, useClass: CoreErrorHandler }]

})
export class SharedLibraryModule { }
