import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { userActivate } from '../../../shared/constants/service.constants';

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', environment.API_SERVER);

@Injectable({ providedIn: 'root' })
export class UserActivationService {
    public userActivate = environment.API_URL + userActivate;

    constructor(private http: HttpClient) { }

    checkUserActivate(key: any): Observable<any> {
        return this.http.get<any>(`${this.userActivate}?key=${key}`, { observe: 'response' });
    }
}
