import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { UserRouteAccessService } from './core';
import { LayoutComponent } from './layout/layout.component';
import { AppComponent } from './main/app.component';

@NgModule({
  imports: [RouterModule.forRoot(
    [
      {
        path: "", redirectTo: "account/login", pathMatch: "full"
      },
      {
        path: "",
        component: AppComponent,
        children: [
          {
            path: "",
            component: LayoutComponent,
            children: [
              {
                path: "", redirectTo: "general-subject", pathMatch: "full"
              },
              {
                path: "general-subject",
                loadChildren: () => import('./general-subject/general-subject.module').then(m => m.GeneralSubjectsModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "General subjects",
                }
              },
              {
                path: "general-concept-and-topic",
                loadChildren: () => import('./general-concept-topic/general-concept-topic.module').then(m => m.GeneralConceptAndTopicModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Genral concept and topic",
                }
              },
              {
                path: "organisation",
                loadChildren: () => import('./organisation/organisation.module').then(m => m.OrganisationModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Organisation",
                }
              },
              {
                path: "curriculum",
                loadChildren: () => import('./curriculum/curriculum.module').then(m => m.CurriculumModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Curriculums",
                }
              },
              {
                path: "subject",
                loadChildren: () => import('./subject/subject.module').then(m => m.SubjectModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Subjects",
                }
              },
              {
                path: "lesson-topic",
                loadChildren: () => import('./syllabus-lesson-topic/syllabus-lesson-topic.module').then(m => m.SyllabusLessonTopicModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Syllabus lesson topic",
                }
              },
              {
                path: "user-admin",
                loadChildren: () => import('./user-admin/user-admin.module').then(m => m.UserAdminModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "User administration",
                }
              },
              {
                path: "base-price-config",
                loadChildren: () => import('./base-price/base-price-config.module').then(m => m.BasePriceConfigModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Base price configuration",
                }
              },
              {
                path: "sales-promotions",
                loadChildren: () => import('./promotions/promotions.module').then(m => m.PromotionsModule),
                canActivate: [UserRouteAccessService],
                data: {
                  pageTitle: "Sales promotions",
                }
              }
            ]
          }
        ]
      }
    ],
    { useHash: true, enableTracing: false }
  )],
  exports: [RouterModule]
})
export class AppRoutingModule { }
