import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { resetPasswordInit } from '../../../shared/constants/service.constants';

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', environment.API_SERVER);

@Injectable({ providedIn: 'root' })
export class PasswordResetInitService {
    constructor(private http: HttpClient) { }

    save(mail: string): Observable<any> {
        return this.http.post(environment.API_URL + resetPasswordInit, mail);
    }
}
